$yellow: #FBBA23;
$blue: #65BB5C;
$pink: #2A2F6A;
$orange: #F23B14;

$duration: 700;

@function randomNum($min, $max) {
  $rand: random();
  $randomNum: $min + floor($rand * (($max - $min) + 1));

  @return $randomNum;
}

.confetti {
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  left: 0;
  bottom: 100%;
  width: 100%;
  height: 100%;
  pointer-events: none;

  &__item {
    position: absolute;
    width: 6%;
    height: 10%;
    background: $yellow;
    bottom: 0;
    opacity: 0;

    @for $i from 1 through 13 {
      &:nth-child(#{$i}) {
        left: $i * 7%;
        transform: rotate(#{randomNum(-180, 180)}deg);
        animation: makeItRain $duration * 1ms ease-out;
        animation-delay: #{randomNum(0, $duration * .5) + 500}ms;
        animation-duration: #{randomNum($duration * .5, $duration * 1)}ms
      }
    }

    &:nth-child(odd) {
      background: $blue;
    }

    &:nth-child(even) {
      z-index: 1;
    }

    &:nth-child(4n) {
      animation-duration: $duration * 2ms;
      background: $orange;
    }

    &:nth-child(3n) {
      animation-duration: $duration * 2.5ms;
      animation-delay: $duration * 1ms;
    }

    &:nth-child(4n-7) {
      background: $pink;
    }
  }
}

@keyframes makeItRain {
  from {
    opacity: 0;
  }
  20% {
    opacity: 1;
  }
  to {
    transform: translateY(-1000%);
  }
}