.box {
  width: 8rem;
  height: 8rem;
  margin: 1.2rem;
  display: flex;
  flex-direction: column;
  cursor: pointer;
  transition: transform .2s;

  &__header {
    width: 100%;
    height: 20%;
    background-color: #fc2c2c;
    position: relative;
    box-shadow: 0 0 2px black;
    z-index: 1;
    margin-bottom: -6%;
    transition: transform 1s;

    &::after {
      content: '';
      position: absolute;
      top: 0;
      right: 20%;
      width: 10%;
      height: 100%;
      background-color: #fd6bc7;
    }
  }

  &__container {
    width: 90%;
    height: 80%;
    background-color: #d111ea;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 0 auto;
    position: relative;
    box-shadow: 0 0 7px -2px black;

    &::after {
      content: '';
      position: absolute;
      top: 0;
      right: 20%;
      width: 10%;
      height: 100%;
      background-color: #ff4c75;
    }
  }

  &__content {
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: -1;
    font-size: 1.4rem;
    font-family: var(--font-family-montserrat-regular);
    color: #f7c5fe;
    margin-right: 20%;
    user-select: none;
  }

  &_main & {
    &__header {
      background-color: #fc2c74;

      &::after {
        background-color: #fb80da;
      }
    }

    &__container {
      background-color: #9211ea;

      &::after {
        background-color: #ff4cdc;
      }
    }
  }

  &:hover:not(&_active) {
    transform: scale(1.05);
  }

  &:hover:not(&_active) & {
    &__header {
      animation: wave .5s;
    }
  }

  &_active & {
    &__header {
      animation: open 1s forwards;
      transform-origin: right;
    }

    &__content {
      animation: appearance 1s .7s forwards linear;
    }
  }
}

@keyframes wave {
  0%, 50%, 100% {
    transform: rotate(0);
  }
  25% {
    transform: rotate(4deg);
    transform-origin: right center;
  }
  75% {
    transform: rotate(-4deg);
    transform-origin: left center;
  }
}

@keyframes open {
  0% {
    transform: none;
  }
  100% {
    transform: rotate(70deg) translate(90%, 50%);
  }
}

@keyframes appearance {
  0% {
    transform: translateY(0) scale(0);
    color: transparent;
  }
  15% {
    color: transparent;
  }
  50% {
    transform: translateY(-100%) scale(1);
  }
  100% {
    transform: translateY(0) scale(1.5);
       z-index: 2;
  }
}

@media (max-width: 450px), (max-height: 450px) {
  .box__content {
    font-size: .6rem;
  }
}